import React, {Component} from 'react';
import '../styles/Summary.css';

class Summary extends Component {

    render(){
        return(
            <div>
                <h1> Plot Summary </h1>
                <p className='Summary'> American car designer Carroll Shelby and driver Kn Miles battle corporate interference and the laws of physics to build a revolutionary race car for Ford in order. </p>
            </div>
        );
    }
}

export default Summary;